.animation {
  vertical-align: sub;
  display: inline-block;
  margin-top: -0.1em;
  margin-bottom: -0.1em;
  border-style: solid;
  border-color: lightgray;
  border-width: 1px;
  height: 1.2em;
}

.animation-window {
  position: relative;
  height: 100%;
  overflow: hidden;
  background-color: white;
}

.animation-gray-list {
  position: absolute;
  color: lightgray;
}

.animation ul {
  position: relative;
  list-style: none;
  top: 0;
  padding: 0;
  margin: 0;
  -webkit-transition: top 300ms;
  transition: top 300ms;
}

.animation li {
  margin: 0 0.2em;
  text-align: center;
}

